.topics {
  margin-bottom: 50px;
}
.topics ul {
  list-style: none;
  padding-left: 0;
}
.topics ul li {
  padding: 20px 0;
  border-bottom: 1px solid #808080;
}
.topics ul li:after {
  content: '';
  display: block;
  clear: both;
}
.topics ul li .author {
  float: left;
  width: 80px;
}
.topics ul li .author img {
  width: 100%;
}
.topics ul li .topic {
  float: left;
  margin-left: 20px;
}


