* {
  box-sizing: border-box;
}
body {
  margin: 0;
}
body .main-content {
  max-width: 940px;
  margin: 0 auto;
}
.nav {
  background: #369;
  padding: 20px 0;
  margin-bottom: 30px;
}
.nav a {
  color: #fff;
}
.main:after {
  content: '';
  display: block;
  clear: both;
}
.main .page-content {
  width: 70%;
  padding-right: 30px;
  float: left;
}
.main .user {
  width: 30%;
  float: left;
}
.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  background: #369;
  padding: 10px 0;
}
.footer p {
  margin: 0;
  color: #fff;
}


